#sandbox {
	display: flex;
	flex-direction: row;
	position: fixed;
	height: 100%;
	width: 100%;
}

#sandbox aside {
	flex-grow: 1;
	margin: 10px;
}

#sandbox .panel-heading {
	padding: 10px 15px;
	border-bottom: 1px solid transparent;
	border-top-left-radius: 3px;
	border-top-right-radius: 3px;
	color: #333;
	background-color: #f5f5f5;
	border-color: #ddd;
	display: flex;
	justify-content: space-between;
}

#sandbox .panel {
	background-color: #fff;
	border: 1px solid transparent;
	border-radius: 4px;
	-webkit-box-shadow: 0 1px 1px rgb(0 0 0 / 5%);
	box-shadow: 0 1px 1px rgb(0 0 0 / 5%);
	border-color: #ddd;
}

#sandbox .func-btn {
	display: inline-block;
	padding: 6px 12px;
	margin-bottom: 0;
	font-size: 14px;
	font-weight: 400;
	line-height: 1.42857143;
	text-align: center;
	white-space: nowrap;
	vertical-align: middle;
	cursor: pointer;
	user-select: none;
	background-image: none;
	border: 1px solid transparent;
	border-radius: 4px;
	outline: unset;
}

#sandbox .color-btn {
	color: #fff;
	background-color: #5cb85c;
	border-color: #4cae4c;
}

#sandbox .default-btn {
	color: #333;
	background-color: #fff;
	border-color: #ccc;
}

#sandbox .form-control {
	display: block;
	width: 100%;
	padding: 6px 12px;
	font-size: 14px;
	line-height: 1.42857143;
	color: #555;
	background-color: #fff;
	background-image: none;
	border-radius: 4px;
	box-shadow: inset 0 1px 1px rgb(0 0 0 / 8%);
	box-sizing: border-box;
	width: 100%;
	border: unset;
	outline: unset;
	resize: unset;
	text-align: left;
	height: calc(100% - 55px);
}

#sandbox iframe {
	width: 100%;
	height: 100%;
}
