.post-list header {
	display: flex;
	justify-content: space-between;
}

.post-list p {
	margin-top: 15px;
	overflow: hidden;
	text-overflow: ellipsis;
	display: -webkit-box;
	-webkit-line-clamp: 2;
	-webkit-box-orient: vertical;
	line-height: 1.6;
	color: #b2bac2;
	font-size: 14px;
	font-family: -apple-system, BlinkMacSystemFont, Helvetica Neue, PingFang SC, Microsoft YaHei, Source Han Sans SC, Noto Sans CJK SC, WenQuanYi Micro Hei, sans-serif;
}

.post-list .infos {
	color: #b2bac2;
	font-size: 14px;
	position: relative;
	display: block;
	text-align: right;
}

.post-list .infos .lphva-icon-fabu2 {
	margin-right: 5px;
}

.post-list .infos .lphva-icon-gengduo1 {
	margin-left: 10px;
	cursor: pointer;
}

.post-list h1 {
	margin: 0;
	font-size: 18px;
	line-height: 1;
	color: #2e3135;
	position: relative;
	vertical-align: middle;
	cursor: pointer;
}

.post-list {
	display: block;
	position: relative;
	padding-bottom: 80px;
}

.post-list::after {
	width: 50%;
	height: 1px;
	margin: 40px auto;
	border: 0;
	background: #efefef;
	position: absolute;
	left: 50%;
	transform: translateX(-50%);
	content: '';
	bottom: 0;
}

.post-list:last-child::after {
	display: none;
}
.notice {
	padding: 20px;
	color: #505050;
	background: #fbfbfb;
	border-radius: 3px;
	margin: 0 auto 40px auto;
	max-width: 800px;
}

.swiper {
	padding-top: 35px;
	display: flex;
	position: relative;
}

.swiper .item {
	position: relative;
	flex: 1;
	cursor: pointer;
}

.swiper .item.wap {
	display: none;
}

.swiper .item:nth-child(2) {
	margin: 0 5px;
}

.swiper img {
	width: 100%;
	object-fit: cover;
	border-radius: 5px;
	display: block;
}

.label-title {
	color: #333;
	font-size: 16px;
	font-weight: 700;
	padding-bottom: 15px;
	border-bottom: 1px dashed #ececec;
	line-height: 1;
}

h2.main-title {
	color: #333;
	font-size: 16px;
	font-weight: 700;
	padding-bottom: 30px;
	border-bottom: 1px dashed #ececec;
	margin: 60px 0;
}

.swiper .mask span {
	position: absolute;
	width: 100%;
	height: 100%;
	background: #bbe2db;
	color: #fff;
	font-size: 16px;
	opacity: 0;
	border-radius: 5px;
	display: flex;
	justify-content: center;
	align-items: center;
}

.swiper .mask:hover span {
	opacity: 1;
}

.foverlay {
	-moz-transition: opacity 0.4s ease-out;
	-o-transition: opacity 0.4s ease-out;
	-webkit-transition: opacity 0.4s ease-out;
	transition: opacity 0.4s ease-out;
}

.pagination-box {
	padding-bottom: 50px;
	text-align: center;
}

.pagination-box .page-container {
	display: inline-block;
	padding: 0;
}
