#toast,
.toast__overlay {
	width: 100%;
	height: 100%;
	top: 0;
	left: 0;
}
#toast {
	position: fixed;
	display: flex;
	align-items: center;
	justify-content: center;
	pointer-events: none;
	z-index: 222;
}
#toast .toast__overlay {
	position: absolute;
	z-index: 1;
	background: rgba(55, 58, 71, 0.9);
	opacity: 0;
	transition: opacity 0.3s;
	-webkit-backface-visibility: hidden;
}
#toast.toast--open .toast__overlay {
	opacity: 1;
	pointer-events: auto;
}
#toast .toast__content {
	width: 50%;
	max-width: 400px;
	min-width: 290px;
	background: #fff;
	padding: 20px;
	text-align: center;
	position: relative;
	z-index: 555;
	opacity: 0;
	border-radius: 8px;
}
#toast.toast--open .toast__content {
	pointer-events: auto;
}
#toast h2 {
	margin: 0;
	font-weight: 500;
	font-size: 14px;
}

#toast .action {
	margin: 0;
	font-weight: 400;
	font-size: 14px;
	color: #1989fa;
	margin-top: 15px;
}

#toast .action span {
	cursor: pointer;
}

#toast.toast--open .toast__content,
#toast.toast--close .toast__content {
	animation-duration: 0.3s;
	animation-fill-mode: forwards;
}
#toast.toast--open .toast__content {
	animation-name: anim-open;
}
#toast.toast--close .toast__content {
	-webkit-animation-name: anim-close;
	animation-name: anim-close;
}

@keyframes anim-open {
	0% {
		opacity: 0;
		transform: scale3d(1.1, 1.1, 1);
	}
	100% {
		opacity: 1;
		transform: scale3d(1, 1, 1);
	}
}

@keyframes anim-close {
	0% {
		opacity: 1;
	}
	100% {
		opacity: 0;
		transform: scale3d(0.9, 0.9, 1);
	}
}
