::-webkit-scrollbar {
	width: 0;
	background: 0 0;
}

::-moz-selection {
	background: rgba(204, 204, 204, 0.18);
	color: #000;
}

::selection {
	background: rgba(204, 204, 204, 0.18);
	color: #000;
}
/* 
::selection {
	background-color: #ffb821;
	color: #fff;
} */

html body {
	_background-attachment: fixed;
}

html {
	width: 100%;
	height: 100%;
	-ms-text-size-adjust: 100%;
	-webkit-text-size-adjust: 100%;
	font-size: 12px;
}

body {
	width: 100%;
	height: auto;
	font-family: Arial, Helvetica, Clean, Sans-serif, Hiragino Sans GB, Hiragino Sans GB W3, Microsoft Yahei, STHeiti;
	color: rgba(2, 0, 0, 0.6);
	margin: 0;
	padding: 0;
	background: #f0f0f0;
}

*,
*:after,
*:before {
	-webkit-box-sizing: border-box;
	-moz-box-sizing: border-box;
	box-sizing: border-box;
}

h1,
h2,
h3,
h4,
h5 {
	margin: 0;
}

blockquote {
	overflow: hidden;
}

img {
	height: auto;
	max-width: 100%;
}

.back-top {
	position: fixed;
	bottom: 50px;
	right: 0;
	z-index: 2;
	display: none;
}

.back-top i {
	font-size: 50px;
}

.blog-description {
	text-transform: none;
	font-weight: 400;
}

.custom-logo {
	display: inline-block;
	width: 100px;
}

.site-branding {
	position: relative;
	display: inline-block;
	z-index: 3;
	padding-top: 100px;
}

#header {
	position: relative;
	z-index: 2;
	display: block;
	color: #fff;
	text-align: center;
	width: 100%;
	height: 480px;
	box-sizing: border-box;
	/* background: url(./assets/47fb3c_.jpg) no-repeat;
	background-size: 100%; */
}

.header {
	position: relative;
	z-index: 2;
	display: block;
	color: #fff;
	text-align: center;
	width: 100%;
	height: 480px;
	box-sizing: border-box;
	background: url(./assets/47fb3c_.jpg) no-repeat;
	background-size: 100%;
}

#header #follow-me {
	display: none;
}

#main {
	position: relative;
	background: #fff;
}

.container {
	position: relative;
	background: #fff;
	width: 800px;
	margin: 0 auto;
}

.decor-wrapper {
	width: 100%;
	height: 50%;
	position: absolute;
	left: 0;
	bottom: 0;
	z-index: 2;
}

.decor {
	position: absolute;
	left: 0;
	width: 100%;
	height: 100%;
	z-index: 2;
}

.decor.bottom {
	bottom: -1px;
}

.decor.top {
	top: -1px;
}

.avatar img {
	border-radius: 50%;
}

article {
	width: 800px;
	margin: 100px auto 0 auto;
}

.top-social {
	height: 32px;
	list-style: none;
	display: inline-block;
	margin-top: 20px;
}

.top-social li {
	float: left;
	margin-right: 10px;
	cursor: pointer;
}

.top-social img {
	height: 32px;
	width: 32px;
	background: #fff;
	border-radius: 50%;
}

#follow-me {
	display: none;
}

.lock-body {
	height: 100%;
	overflow: hidden;
}
@media (max-width: 640px) {
	#header {
		display: none;
	}
	#follow-me {
		display: flex;
		background-color: #fff;
		padding: 10px;
		justify-content: space-between;
		align-items: center;
		margin-bottom: 10px;
		position: sticky;
		top: 0;
		z-index: 1;
	}
	#follow-me .flex-left {
		flex: 1;
		display: flex;
		font-size: 14px;
	}
	#follow-me .flex-right {
		flex: 1;
		text-align: right;
	}
	#follow-me .icon-box {
		width: 40px;
		height: 40px;
		border-radius: 50%;
		display: flex;
		justify-content: center;
		align-items: center;
		background-color: #30cf30;
		margin-right: 10px;
	}
	#follow-me .icon-box .iconfont {
		color: #fff;
		font-size: 25px;
	}
	#follow-me .add_button {
		display: inline-flex;
		cursor: pointer;
		box-sizing: border-box;
		font-weight: 500;
		padding: 12px 20px;
		font-size: 14px;
		border-radius: 13px;
		height: 26px;
		justify-content: center;
		align-items: center;
		background-color: #3285ff;
		color: #fff;
	}
	.container {
		width: 100%;
		padding: 0 10px;
	}
	.notice {
		font-size: 14px;
		margin-bottom: 0;
		background: #fff;
		border-bottom: 10px solid #f0f0f0;
	}
	.label-title {
		display: none;
	}

	.post-list .infos {
		display: none;
	}
	.post-list::after {
		width: 100%;
	}
	.detail {
		padding-top: 20px;
	}
	.back-top {
		display: none !important;
	}
	.post-list::after {
		margin: 25px auto;
	}
	.post-list {
		padding-bottom: 30px;
	}
	.swiper-container {
		overflow: hidden;
		border-bottom: 10px solid #f0f0f0;
		padding: 0;
		width: 100vw;
	}
	.swiper-container .swiper {
		width: 500vw;
		position: relative;
		animation: slideout 15s infinite;
		animation-play-state: running;
		border-radius: 0;
		padding-top: 0;
	}
	.swiper-container .swiper img {
		border-radius: 0;
	}
	.swiper-container .swiper .item {
		float: left;
		width: 100vw;
		margin: 0;
	}
	.swiper .item.wap {
		display: block;
	}
	h2.main-title {
		margin: 20px auto;
		padding-bottom: 20px;
		font-size: 20px;
	}
	.swiper:hover {
		animation-play-state: paused;
		cursor: pointer;
	}
	.post-list h1 {
		font-size: 14px;
	}
	.post-list p {
		font-size: 12px;
		margin-top: 8px;
		line-height: 1.4;
	}
	#follow-me .nickname {
		color: #333;
		word-break: break-all;
		font-weight: 700;
	}
	#follow-me .wechat {
		color: #999;
		word-break: break-all;
		font-size: 12px;
		margin-top: 3px;
	}
	#follow-me .content {
		display: inline-flex;
		flex-direction: column;
		line-height: 1;
		justify-content: center;
	}
	#toast .toast__content {
		width: 50%;
		max-width: 700px;
		min-width: 290px;
		background: #fff;
		padding: 20px;
		text-align: center;
		position: relative;
		z-index: 555;
		opacity: 0;
		border-radius: 10px;
	}
}

@keyframes slideout {
	0% {
		transform: translateX(0px);
	}
	5% {
		transform: translateX(calc(-100vw * 1)) scale(1);
	}
	25% {
		transform: translateX(calc(-100vw * 1)) scale(1);
	}
	30% {
		transform: translateX(calc(-100vw * 2)) scale(1);
	}
	50% {
		transform: translateX(calc(-100vw * 2)) scale(1);
	}
	55% {
		transform: translateX(calc(-100vw * 3)) scale(1);
	}
	75% {
		transform: translateX(calc(-100vw * 3)) scale(1);
	}
	80% {
		transform: translateX(calc(-100vw * 4)) scale(1);
	}
	100% {
		transform: translateX(calc(-100vw * 4)) scale(1);
	}
}

.clearselect {
	-webkit-touch-callout: none; /* iOS Safari */
	-webkit-user-select: none; /* Chrome/Safari/Opera */
	-khtml-user-select: none; /* Konqueror */
	-moz-user-select: none; /* Firefox */
	-ms-user-select: none; /* Internet Explorer/Edge */
	user-select: none; /* Non-prefixed version, currently
	not supported by any browser */
}
