.page-container:after {
	content: '.';
	display: block;
	height: 0;
	overflow: hidden;
	clear: both;
}
.page-container {
	zoom: 1;
}

.page-container li {
	padding: 0 4px;
	background: #fff;
	vertical-align: top;
	display: inline-block;
	font-size: 16px;
	min-width: 35.5px;
	height: 28px;
	line-height: 28px;
	cursor: pointer;
	box-sizing: border-box;
	text-align: center;
	margin: 0;
	list-style: none;
	float: left;
	padding: 0px 8px;
	cursor: pointer;
	height: 28px;
	line-height: 28px;
	margin-right: 8px;
	font-weight: 600;
}

.page-container li:last-child {
	margin-right: 0px;
}

/*当前页样式*/
.page-container li.activePage {
	color: #409eff;
	cursor: default;
}

/*当前页样式*/
.page-container li:hover {
	color: #409eff;
}

/*没有上一页和下一页时样式*/
.nomore {
	color: #b5b5b5 !important;
	cursor: not-allowed !important;
}
