	line-height: 1;
	color: #2e3135;
	position: relative;
	vertical-align: middle;
	cursor: pointer;
}

.post-list {
	display: block;
	position: relative;
	padding-bottom: 80px;
}

.post-list::after {
	width: 50%;
	height: 1px;
	margin: 40px auto;
	border: 0;
	background: #efefef;
	position: absolute;
	left: 50%;
	transform: translateX(-50%);
	content: '';
	bottom: 0;
}

.post-list:last-child::after {
	display: none;
}
.notice {
	padding: 20px;
	color: #505050;
	background: #fbfbfb;
	border-radius: 3px;
	margin: 0 auto 40px auto;
	max-width: 800px;
}

.swiper {
	padding-top: 35px;
	display: flex;
	position: relative;
}

.swiper .item {
	position: relative;
	flex: 1 1;
	cursor: pointer;
}

.swiper .item.wap {
	display: none;
}

.swiper .item:nth-child(2) {
	margin: 0 5px;
}

.swiper img {
	width: 100%;
	object-fit: cover;
	border-radius: 5px;
	display: block;
}

.label-title {
	color: #333;
	font-size: 16px;
	font-weight: 700;
	padding-bottom: 15px;
	border-bottom: 1px dashed #ececec;
	line-height: 1;
}

h2.main-title {
	color: #333;
	font-size: 16px;
	font-weight: 700;
	padding-bottom: 30px;
	border-bottom: 1px dashed #ececec;
	margin: 60px 0;
}

.swiper .mask span {
	position: absolute;
	width: 100%;
	height: 100%;
	background: #bbe2db;
	color: #fff;
	font-size: 16px;
	opacity: 0;
	border-radius: 5px;
	display: flex;
	justify-content: center;
	align-items: center;
}

.swiper .mask:hover span {
	opacity: 1;
}

.foverlay {
	transition: opacity 0.4s ease-out;
}

.pagination-box {
	padding-bottom: 50px;
	text-align: center;
}

.pagination-box .page-container {
	display: inline-block;
	padding: 0;
}

.page-container:after {
	content: '.';
	display: block;
	height: 0;
	overflow: hidden;
	clear: both;
}
.page-container {
	zoom: 1;
}

.page-container li {
	padding: 0 4px;
	background: #fff;
	vertical-align: top;
	display: inline-block;
	font-size: 16px;
	min-width: 35.5px;
	height: 28px;
	line-height: 28px;
	cursor: pointer;
	box-sizing: border-box;
	text-align: center;
	margin: 0;
	list-style: none;
	float: left;
	padding: 0px 8px;
	cursor: pointer;
	height: 28px;
	line-height: 28px;
	margin-right: 8px;
	font-weight: 600;
}

.page-container li:last-child {
	margin-right: 0px;
}

/*当前页样式*/
.page-container li.activePage {
	color: #409eff;
	cursor: default;
}

/*当前页样式*/
.page-container li:hover {
	color: #409eff;
}

/*没有上一页和下一页时样式*/
.nomore {
	color: #b5b5b5 !important;
	cursor: not-allowed !important;
}

