}

#header #follow-me {
	display: none;
}

#main {
	position: relative;
	background: #fff;
}

.container {
	position: relative;
	background: #fff;
	width: 800px;
	margin: 0 auto;
}

.decor-wrapper {
	width: 100%;
	height: 50%;
	position: absolute;
	left: 0;
	bottom: 0;
	z-index: 2;
