}
@media (max-width: 640px) {
	#header {
		display: none;
	}
	#follow-me {
		display: flex;
		background-color: #fff;
		padding: 10px;
		justify-content: space-between;
		align-items: center;
		margin-bottom: 10px;
		position: -webkit-sticky;
		position: sticky;
		top: 0;
		z-index: 1;
	}
	#follow-me .flex-left {
		flex: 1 1;
		display: flex;
		font-size: 14px;
	}
	#follow-me .flex-right {
		flex: 1 1;
		text-align: right;
	}
	#follow-me .icon-box {
		width: 40px;
		height: 40px;
		border-radius: 50%;
		display: flex;
		justify-content: center;
		align-items: center;
		background-color: #30cf30;
		margin-right: 10px;
	}
	#follow-me .icon-box .iconfont {
		color: #fff;
		font-size: 25px;
	}
	#follow-me .add_button {
		display: inline-flex;
		cursor: pointer;
		box-sizing: border-box;
		font-weight: 500;
		padding: 12px 20px;
		font-size: 14px;
		border-radius: 13px;
		height: 26px;
		justify-content: center;
		align-items: center;
		background-color: #3285ff;
		color: #fff;
	}
	.container {
		width: 100%;
		padding: 0 10px;
	}
	.notice {
		font-size: 14px;
		margin-bottom: 0;
		background: #fff;
		border-bottom: 10px solid #f0f0f0;
	}
	.label-title {
		display: none;
	}

	.post-list .infos {
		display: none;
	}
	.post-list::after {
		width: 100%;
	}
	.detail {
		padding-top: 20px;
	}
	.back-top {
		display: none !important;
	}
	.post-list::after {
		margin: 25px auto;
	}
	.post-list {
		padding-bottom: 30px;
	}
	.swiper-container {
		overflow: hidden;
		border-bottom: 10px solid #f0f0f0;
		padding: 0;
		width: 100vw;
	}
	.swiper-container .swiper {
		width: 500vw;
		position: relative;
		animation: slideout 15s infinite;
		animation-play-state: running;
		border-radius: 0;
		padding-top: 0;
	}
	.swiper-container .swiper img {
		border-radius: 0;
	}
	.swiper-container .swiper .item {
		float: left;
		width: 100vw;
		margin: 0;
	}
	.swiper .item.wap {
		display: block;
	}
	h2.main-title {
		margin: 20px auto;
		padding-bottom: 20px;
		font-size: 20px;
	}
	.swiper:hover {
		animation-play-state: paused;
		cursor: pointer;
	}
	.post-list h1 {
		font-size: 14px;
	}
	.post-list p {
		font-size: 12px;
		margin-top: 8px;
		line-height: 1.4;
	}
	#follow-me .nickname {
		color: #333;
		word-break: break-all;
		font-weight: 700;
	}
	#follow-me .wechat {
		color: #999;
		word-break: break-all;
		font-size: 12px;
		margin-top: 3px;
	}
	#follow-me .content {
		display: inline-flex;
		flex-direction: column;
		line-height: 1;
		justify-content: center;
	}
	#toast .toast__content {
		width: 50%;
		max-width: 700px;
		min-width: 290px;
		background: #fff;
		padding: 20px;
		text-align: center;
		position: relative;
		z-index: 555;
		opacity: 0;
		border-radius: 10px;
	}
}

@keyframes slideout {
	0% {
		transform: translateX(0px);
	}
	5% {
		transform: translateX(calc(-100vw * 1)) scale(1);
	}
	25% {
		transform: translateX(calc(-100vw * 1)) scale(1);
	}
	30% {
		transform: translateX(calc(-100vw * 2)) scale(1);
	}
	50% {
		transform: translateX(calc(-100vw * 2)) scale(1);
	}
	55% {
		transform: translateX(calc(-100vw * 3)) scale(1);
	}
	75% {
		transform: translateX(calc(-100vw * 3)) scale(1);
	}
	80% {
		transform: translateX(calc(-100vw * 4)) scale(1);
	}
	100% {
		transform: translateX(calc(-100vw * 4)) scale(1);
	}
}

.clearselect {
	-webkit-touch-callout: none; /* iOS Safari */
	-webkit-user-select: none; /* Chrome/Safari/Opera */ /* Konqueror */ /* Firefox */
	-ms-user-select: none; /* Internet Explorer/Edge */
	user-select: none; /* Non-prefixed version, currently
	not supported by any browser */
}

#toast,
.toast__overlay {
	width: 100%;
	height: 100%;
	top: 0;
	left: 0;
}
#toast {
	position: fixed;
	display: flex;
	align-items: center;
	justify-content: center;
	pointer-events: none;
	z-index: 222;
}
#toast .toast__overlay {
	position: absolute;
	z-index: 1;
	background: rgba(55, 58, 71, 0.9);
	opacity: 0;
	transition: opacity 0.3s;
	-webkit-backface-visibility: hidden;
}
#toast.toast--open .toast__overlay {
	opacity: 1;
	pointer-events: auto;
}
#toast .toast__content {
	width: 50%;
	max-width: 400px;
	min-width: 290px;
	background: #fff;
	padding: 20px;
	text-align: center;
	position: relative;
	z-index: 555;
	opacity: 0;
	border-radius: 8px;
}
#toast.toast--open .toast__content {
	pointer-events: auto;
}
#toast h2 {
	margin: 0;
	font-weight: 500;
	font-size: 14px;
}

#toast .action {
	margin: 0;
	font-weight: 400;
	font-size: 14px;
	color: #1989fa;
	margin-top: 15px;
}

#toast .action span {
	cursor: pointer;
}

#toast.toast--open .toast__content,
#toast.toast--close .toast__content {
	animation-duration: 0.3s;
	animation-fill-mode: forwards;
}
#toast.toast--open .toast__content {
	animation-name: anim-open;
}
#toast.toast--close .toast__content {
	animation-name: anim-close;
}

@keyframes anim-open {
	0% {
		opacity: 0;
		transform: scale3d(1.1, 1.1, 1);
	}
	100% {
		opacity: 1;
		transform: scale3d(1, 1, 1);
	}
}

@keyframes anim-close {
	0% {
		opacity: 1;
	}
	100% {
		opacity: 0;
		transform: scale3d(0.9, 0.9, 1);
	}
}

